.main-nav {
  position: relative; }
  .main-nav__toggle {
    background-color: transparent;
    border: none;
    cursor: pointer;
    position: fixed;
    top: 12px;
    left: 20px;
    z-index: 2;
    display: block;
    width: 40px;
    height: 40px;
    background-color: #ffffff;
    border-radius: 50%; }
    .main-nav__toggle:hover::before, .main-nav__toggle:hover::after, .main-nav__toggle:focus::before, .main-nav__toggle:focus::after {
      opacity: 0.6; }
    .main-nav__toggle:active::before, .main-nav__toggle:active::after {
      opacity: 0.3; }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 10px;
    right: 8px;
    width: 24px;
    height: 2px;
    background-color: #00c0ff;
    box-shadow: 0 8px 0 0 #00c0ff, 0 16px 0 0 #00c0ff; }
  .main-nav--closed .main-nav__list {
    display: none; }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 18px;
    left: 8px;
    width: 24px;
    height: 2px;
    background-color: #00c0ff; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg); }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }
  .main-nav__list {
    position: fixed;
    top: 30px;
    left: 60px;
    z-index: 2;
    background-color: #ffffff; }

@media (min-width: 450px) {
  .main-nav__toggle {
    left: 15px; }
  .main-nav__list {
    left: 55px; } }

@media (min-width: 926px) {
  .main-nav {
    margin-top: 10px; }
    .main-nav__toggle {
      position: static; }
    .main-nav__list {
      position: absolute;
      top: 17px;
      left: 35px; } }

